.popup {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  overflow: hidden;
  position: fixed;
  top: 0px;
  display: flex;
  font-family: Inter, Arial, Helvetica, sans-serif;
  align-items: center;
  visibility: hidden;
  opacity: 0;
  transition: all 1s ease 0s;
}
